import {EventEmitter, Injectable} from "@angular/core";
import {map} from "rxjs/operators";
import {HttpClient} from "@angular/common/http";
import {Task} from "./task.model";

@Injectable()
export class TaskService {

    onTaskAdded = new EventEmitter<Task>();
    onTaskDeleted = new EventEmitter<Task>();

    constructor(private http : HttpClient) {

    }

    getTasks() {

        return this.http.get('/api/tasks').pipe(map(data => data ));

    }

    saveTask(task: Task, checked: boolean) {

        task.completed = checked;
        return this.http.post('/api/tasks/addnewTask', task).pipe(map(data => data));

    }

    addTask(task: Task) {

        return this.http.post('/api/tasks/addnewTask', task).pipe(map(data => data));

    }

    delete(task: Task) {

        return this.http.delete('/api/tasks/deleteTask/'+task.id).subscribe();

    }

}
